$max-width: 600px;

.App {
  background-color: #282c34;
  color: #fff;
  text-align: center;
  min-height: 100vh;
  padding-bottom: 40px;
}

.content {
  padding: 0 50px;
}

.header {
  padding: 20px 0;

  .headline {
    font-size: 60px;
    font-weight: 700;
  }
}

.footer {
  position: sticky;
  bottom: 0;
}

.config {
  max-width: $max-width;
  margin: auto;

  .text {
    color: #000;
    width: 100%;
    height: 180px;
  }
}

.drawArea {
  position: relative;
  height: 400px;
  transform: scale(0.6);
  transform-origin: top center;

  .canvas {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;

    &:nth-child(1) {
      transform: translateX(-100%);
    }

    &:nth-child(2) {
      transform: translateX(100%);
    }
  }
}

.config-field {
  text-align: left;
}

.result {
  font-family: monospace;
}

.result,
#diff {
  transition: opacity 0.3s ease;
}

.upload-icon {
  cursor: pointer;
  user-select: none;
  border-color: #ced4da;

  &:hover {
    border-color: #ced4da;
  }

  span {
    transform-origin: 50% 60%;
    transform: scale(2.4, 1.8);
  }
}

.uploadFont {
  display: none;
}
